/**
 * This class aims to store the shared props
 */
export class PropsService {
  
  /**
   * report parameters
   * all the paraemters are listed in: https://devtopia.esri.com/Beijing-R-D-Center/feature-report/issues/505#issuecomment-4495107
   */

  // token, required
  static token: string;

  static portalUrl?: string;

  static apiUrl?: string;

  // Feature layer or table URL. required
  static featureLayerUrl: string;

  static surveyItemId?: string;

  static templateItemId?: string;

  static queryParameters: string;  // Object string
  
  static mergeFiles: 'none' | 'nextPage' | 'continuous';

  static outputFormat: string;
  
  static outputReportName: string;
  
  static outputPackageName: string;
  
  static packageFiles: boolean | string;
  
  static uploadInfo: string;   // Object string
  
  static webmapItemId: string;
  
  static mapScale: string;

  static locale?: string;

  static utcOffset?: string;

  static reportTemplateIds?: string;

  static clientId?: string;

  static requestSource: string;


  /**
   * the following parameters are geneareted by the main parameters, ie:
   * the where is genereated from queryParameters
   * the username is generated from token 
   */
  static where: string;

  static username?: string;

  /**
   * ui parameters
   */
  static show: string;

  static hide: string;

  static inputFeatureTemplate: string;

  static label: string;

  // /**
  //  * @internal
  //  * @param metadata 
  //  */
  // constructor(metadata?: any) {
  //   const defaultValue = {
  //     url: 'https://survey123.arcgis.com/api/featureReport',
  //     itemId: null,
  //     token: null,
  //     portalUrl: 'https://www.arcgis.com',
  //     f: 'json'
  //   };

  //   Object.assign(this, defaultValue, metadata || {});
  // }

  static paramesWhitelist: string[] = ['token', 'portalUrl', 'apiUrl', 'featureLayerUrl', 'surveyItemId', 
  'templateItemId', 'queryParameters', 'mergeFiles', 'outputFormat', 'outputReportName', 
  'outputPackageName', 'packageFiles', 'uploadInfo', 'webmapItemId', 'mapScale', 
  'locale', 'utcOffset', 'where', 'username', 'show', 'hide', 'inputFeatureTemplate', 'label', 'reportTemplateIds', 'clientId', 'requestSource'];
  /**
   * set props
   * @param obj 
   */
  static setProps(obj) {
    const whitelist = PropsService.paramesWhitelist;

    let env = '';
    const pattern = /survey123(.*?).arcgis.com/;
    const match = window.location.hostname.match(pattern);
    if (match && match[1]) {
      if (['dev', 'qa', 'beta'].includes(match[1])) {
        env = match[1];
      }
    }

    const defaultLocale = this.getLocale();
    const defaultValue = {
      apiUrl: `https://survey123${env}.arcgis.com/api/featureReport`,
      itemId: null,
      token: null,
      outputFormat: 'docx',
      portalUrl: 'https://www.arcgis.com',
      locale: defaultLocale,
      f: 'json'
    };

    if (typeof obj === 'object') {
      for (let key in obj) {
        if (whitelist.indexOf(key) >= 0) {
          PropsService[key] = obj[key] || defaultValue[key] || obj[key];
          if (key === 'locale' && obj[key]) {
            PropsService[key] = this.convertToSupportedLang(obj[key]);
          }
        }
      }
      // Object.assign(this, obj || {});
    }

    if (PropsService.outputFormat !== 'docx' && PropsService.outputFormat !== 'pdf') {
      PropsService.outputFormat = 'docx';
    }
  }

  /**
   * get locale
   * @returns 
   */
  static getLocale(options?: {
    userDefinedLocale?: string;
    userInfo?: any;
    portalInfo?: any;
  }) {
    options = Object.assign({

    }, options || {});

    let userInfo = options.userInfo || {};
    let portalInfo = options.portalInfo || {};

    const browerLocale =  navigator.language || (navigator as any).userLanguage;

    // https://devtopia.esri.com/Beijing-R-D-Center/survey123-webform/issues/857
    return this.convertToSupportedLang(options.userDefinedLocale)  ||
      this.convertToSupportedLang(userInfo.culture) ||
      this.convertToSupportedLang(portalInfo.culture) ||
      this.convertToSupportedLang(browerLocale) || 'en';
  }

  /**
  * convert the provided language code to the supported language code
  * @param lang 
  */
   static convertToSupportedLang(lang) {
      const supportLangs: string[] = [
        "ar",
        "bg",
        "bs",
        "ca",
        "cs",
        "da",
        "de",
        "el",
        "en",
        "es",
        "et",
        "fi",
        "fr",
        "he",
        "hr",
        "hu",
        "id",
        "it",
        "ja",
        "ko",
        "lt",
        "lv",
        "nb",
        "nl",
        "pl",
        "pt-br",
        "pt-pt",
        "ro",
        "ru",
        "sk",
        "sl",
        "sr",
        "sv",
        "th",
        "tr",
        "uk",
        "vi",
        "zh-cn",
        "zh-hk",
        "zh-tw"
      ];
      const supportSubLocales = [
        "en-US",
        "en-GB",
        "en-AU",
        "en-CA",
        "es-ES",
        "es-MX",
        "it-CH",
        "de-CH",
        "de-AT",
        "fr-CH"
      ];
   lang = lang || '';
   lang = (lang + '').toLowerCase();
 
   const isSupportLangs = supportLangs.find((e) => {
     return e.toLowerCase() === lang || e.toLowerCase() === lang.split('-')[0] || e.toLowerCase().split('-')[0] === lang;
   });
 
   const isSupportSubLocale = supportSubLocales.find((e) => {
     return e.toLowerCase() === lang;
   });
   return isSupportSubLocale || isSupportLangs;
 }
}